/* Mobile First стили */
.layout {
  min-height: 100vh;
  background-color: #f4f4f4;
}

.site-header {
  background: #2c3e50 !important;
  padding: 0 16px !important;
  line-height: normal !important;
  height: auto !important;
}

.site-title {
  color: white;
  text-align: center;
  margin: 0;
  font-size: 20px;
  padding: 8px 0;
}

.header-menu {
  background: transparent !important;
  justify-content: flex-end;
  border: none !important;
}

.header-menu .ant-menu-item {
  padding: 0 8px !important;
}

.content {
  padding: 16px 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

.content-row {
  display: flex;
  flex-direction: column;
}

/* Mobile order: Таблица → Ссылки */
.order-mobile:first-child {
  order: 1;
}

.order-mobile:last-child {
  order: 2;
}

.section-card {
  margin-bottom: 16px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  height: 100%;
}

.section-card .ant-card-head {
  border-bottom: 1px solid #f0f0f0;
  padding: 12px 16px;
}

.section-card .ant-card-body {
  padding: 16px;
}

.section-title {
  color: #2c3e50;
  margin: 0;
  font-size: 18px;
}

/* Список ссылок */
.links-list .ant-list-item {
  padding: 8px 0;
  border: none !important;
}

.links-list .ant-list-item:not(:last-child) {
  border-bottom: 1px solid #f0f0f0 !important;
}

.links-list a {
  color: #1890ff;
  text-decoration: none;
}

.links-list a:hover {
  text-decoration: underline;
}

.links-list ol {
  margin: 8px 0 0 16px;
  padding: 0;
}

.links-list li {
  margin-bottom: 4px;
}

/* Таблица */
.data-table {
  width: 100%;
}

.data-table .ant-table-thead > tr > th {
  background-color: #34495e;
  color: white;
  font-weight: bold;
  white-space: nowrap;
}

.data-table .ant-table-tbody > tr:nth-child(even) {
  background-color: #f8f9fa;
}

/* Форма */
.contact-form .ant-form-item {
  margin-bottom: 16px;
}

.radio-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.contact-form .ant-btn {
  width: 100%;
}

/* Подвал */
.site-footer {
  background: #2c3e50;
  color: white;
  text-align: center;
  padding: 16px 0;
}

/* Планшеты (768px и выше) */
@media (min-width: 768px) {
  .container {
    max-width: 720px;
    padding: 0 24px;
  }
  
  .site-title {
    font-size: 24px;
    padding: 16px 0;
  }
  
  .content {
    padding: 24px 0;
  }
  
  .section-card {
    margin-bottom: 24px;
  }
  
  .section-card .ant-card-head {
    padding: 16px 24px;
  }
  
  .section-card .ant-card-body {
    padding: 24px;
  }
  
  .section-title {
    font-size: 20px;
  }
  
  .radio-group {
    flex-direction: row;
    gap: 16px;
  }
  
  .contact-form .ant-btn {
    width: auto;
  }
}

/* Десктоп (1024px и выше) */
@media (min-width: 1024px) {
  .container {
    max-width: 960px;
  }
  
  .content-row {
    flex-direction: row;
  }
  
  /* Возвращаем исходный порядок для десктопа */
  .order-mobile:first-child {
    order: 0;
  }
  
  .order-mobile:last-child {
    order: 0;
  }
  
  .site-title {
    font-size: 28px;
  }
}

/* Большие экраны (1200px и выше) */
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}